.container{
    width: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap:60px;

}
.container button{
    border-radius: white 1px solid;
    background-color: rgb(252, 147, 147);
    text-decoration: none;
    color: white;
    border-radius: 2px;
}
.container h5{
    color:white;
    font-size: 20px;
    text-decoration: none;
}
.container h5:hover{
    color:rgb(255, 255, 255);
    font-size: 22px;
    text-decoration: none;
}