.container{
    display: flex;
    justify-content: center;
}
.container form{
    margin: 40px 0;
    border-left: 1px solid black;
    padding-left: 20px;
    
}
.container form label{
    font-size: 20px;

}
.container form input{
    font-size: 20px;
    border-radius: 5px;
    border: .5px black solid;
}
.container form textarea{
    font-size: 20px;
    border-radius: 5px;
    border: .5px black solid;
}
.container button{
    width: 80px;
    height: 40px;
    margin: 10px 20px;
}
.container button:hover{
    cursor: pointer;
    width: 120px;
}
@media (max-width: 600px) {
    .container form{
        margin: 0;
        padding-left: 10px;
        width: 80%;
    }
  }