nav {
    width: 95%;
    margin: 0 auto;
    background: #fff;
    padding: 0;
    box-shadow: 0px 5px 0px #dedede;
  }
  
  /* By Dominik Biedebach @domobch */
  nav ul {
    list-style: none;
    text-align: center;
  }
  nav ul li {
    display: inline-block;
    margin: 0 60px;
  }
  nav ul li a {
    display: block;
    padding: 15px;
    text-decoration: none;
    color: #aaa;
    font-weight: 800;
    text-transform: uppercase;
    margin: 0 10px;
  }
  nav ul li a,
  nav ul li a:after,
  nav ul li a:before {
    transition: all .5s;
  }
  nav ul li a:hover {
    color: #555;
  }

  @media (max-width: 600px) {
    nav ul li {
      display: inline-block;
      margin: 0 20px;
    }
    nav ul li a{
      display: inline-block;
      padding: 5px;
    }
  }