.container{
    display: flex;
    width: 45%;
    align-items: center;
    justify-content: flex-start;
    border-radius: 4px;
    border: 1px black solid;
    flex-direction: row;
    margin: 0 auto;
    background-color: blanchedalmond;
    align-items: center;
    padding: 0 0 0 25px; 
    gap: 40px;
}
.container section{
    margin: 0 40px;
}
.container div button{
    width: 100%;
    background-color: white;
    border-radius: 5px;    
    height: 40px;
    margin: 5px;
}
.container div button:hover{
    width: 120%;
    cursor: pointer;
}
.buttons{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 600px) {
    .container {
        flex-direction: column;
        margin: 10px auto;
    }
}